:host {
  display: block;
}

.video-container {
  position: relative;
  width: 100%;
  padding-top: 62%;
  overflow: hidden;
  border-radius: 8px;
}

video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

img {
  width: 100%;
  border-radius: 8px;
}
